import Particle from "../../graphic/particle";

export namespace BuildUtil {

    export type RunParticleProps = {
        /**  */
        limitFrame: number,
        /**  */
        limitParticle: number,
        /**  */
        guaranteeFrame?: number,
        /** 1 */
        doIterate: (frame: number, owner: Particle.Instance[]) => void,
        /**  */
        doEnd?: (frame: number, owner: Particle.Instance[]) => void
    };

    export type DivideProps = {
        num: number;
        standbyEvent: () => void;
        countUpEvent: () => void;
    }
    export const run = (props: RunParticleProps) => {
        const minFrame = props.guaranteeFrame ?? 0;
        const owner: Particle.Instance[] = [];
        let frame = 0;

        while (true) {
            props.doIterate(frame, owner);

            for (let i = owner.length - 1; i >= 0; i--) {
                const particle = owner[i];

                // 
                particle.update();

                // 
                if (!particle.isLive) {
                    owner.splice(i, 1);
                }
            }

            if (frame > props.limitFrame ||
                owner.length > props.limitParticle ||
                frame > minFrame && owner.length === 0
            ) break;
            frame++;
        };

        if (props.doEnd != undefined) {
            props.doEnd(frame, owner);
        }
    }

    export const runPaging = (props: RunParticleProps, owner: Particle.Instance[], beginFrame: number, advanceFrame: number) => {
        const minFrame = props.guaranteeFrame ?? 0;
        // const owner: Particle.Instance[] = [];

        let frame = beginFrame;
        while (frame - beginFrame < advanceFrame) {
            props.doIterate(frame, owner);

            for (let i = owner.length - 1; i >= 0; i--) {
                const particle = owner[i];

                // 
                particle.update();

                // 
                if (!particle.isLive) {
                    owner.splice(i, 1);
                }
            }

            if (frame > props.limitFrame ||
                owner.length > props.limitParticle ||
                frame > minFrame && owner.length === 0
            ) {
                if (props.doEnd != undefined) {
                    props.doEnd(frame, owner);
                }
                break;
            }
            frame++;
        };
    }
}