import { useContext, useEffect, useState } from "react";
import styled, { css } from "styled-components";
import { TabbedPane, TabElements } from "../../base/components/tab/tabbedPane";
import Styles, { _CSS_LABEL_MIDIUM } from "../../base/design/styles";
import { SCORE_TAB_MARGIN } from "../../utils/systemConst";
import { GlobalContext } from "../entry/systemEntry";
import LogicMainteTab from "./factory/logicMainteTab";
import StoryTab from "./launcher/storyTab";
import ManageTab from "./manage/manageTab";

const CreateChannel = () => {

    const { store, setStore } = useContext(GlobalContext);

    const getProjectType = () => {
        let str = '-';
        if (store.libraryData != null) str = 'library';
        if (store.storyData != null) str = 'scenario';
        return str;
    };
    const projectType = getProjectType();

    const tabElements: TabElements[] = [];
    tabElements.push({ name: 'Manage', enable: true, cont: <ManageTab /> });
    tabElements.push({ name: 'Story', enable: projectType === 'scenario', cont: projectType === 'scenario' ? <StoryTab /> : <></> });
    if (store.env.isEditLibrary) {
        tabElements.push({ name: 'Library', enable: projectType === 'library', cont: <LogicMainteTab /> });
    }
    return (
        <_Wrap>
            <TabbedPane
                tabElements={tabElements}
                activeNo={0}
            // selectTabIndex={(index: number) => dispatcher.score.setScoreTabIndex(index)}
            />
        </_Wrap>
    );
}
export default CreateChannel;

const _Wrap = styled.div`
    display: inline-block;
    position: relative;
    margin: ${SCORE_TAB_MARGIN}px 0 0 ${SCORE_TAB_MARGIN}px;
    width: calc(100% - ${SCORE_TAB_MARGIN * 2}px);
    height: calc(100% - ${SCORE_TAB_MARGIN * 2}px);
    background-color: transparent;
    /* background-color: #c3d8dd6f; */
    /* border: solid 1px black;
    border-radius: 5px; */
    /* box-shadow: 0 2px 14px 4px #000b3a48; */
`;

